import { auth } from '@/auth';
import { fetchHistoricalEvents } from '@/lib/external-data';
import dbConnect from '@/lib/db';
import User from '@/models/User';
import HistoryClient from './HistoryClient';

export default async function HistoryPage() {
  const session = await auth();
  await dbConnect();
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const user: any = session?.user?.email ? await User.findOne({ email: session.user.email }) : null;
  const savedIds: string[] = user?.savedContentIds || [];

  const historyContent = await fetchHistoricalEvents();

  return (
    <div className="pt-4 pb-4">
      <header className="mb-2 px-4">
        <h1 className="text-2xl font-bold text-accent-yellow mb-1">Time Machine</h1>
        <p className="text-gray-400 text-sm">Where the past comes alive.</p>
      </header>

      <HistoryClient initialContent={historyContent} savedIds={savedIds} />
    </div>
  );
}
